
.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.2s;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal__content {
    padding: 11px;
    border-radius: 9px;
    background-color: darkseagreen;
    width: 600px;
    transform: scale(0.8);
    transition: 0.2s all;
}

.modal__content.active {
    transform: scale(1);
}

